body {
  background:url(./images/baground.jpg) no-repeat 50% 50%;
  background-size: cover;
  background-blend-mode: lighten;

}

.App {
  background-color: blue;
  min-height: 500px;
  width: 400px;
  margin: 100px auto; 
  border-radius: 40px;
}

#to-do-form input{
  background-color: floralwhite;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 10px;
}

#to-do-form input::placeholder{
  color: black;
  font-size: 18px;
  font-family: Georgia, 'Times New Roman', Times, serif;

}

#to-do-form input:focus, #to-do-form button:focus{
  outline: none;
}

#to-do-form button{
  height:50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:yellow;
  cursor: pointer;
  font-size: 18px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-weight: bolder;
}
